import { useEffect, useState } from "react";
import { Link, Navigate, useSearchParams, useNavigate } from "react-router-dom";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { useFirebaseAuth } from "@/contexts/FirebaseAuthContext";
import { useToast } from "@/hooks/use-toast";
import { Smartphone, Monitor, Download } from "lucide-react";
import { db } from "@/firebase";
import { collection, addDoc, serverTimestamp } from "firebase/firestore";
import { getAppDownloads, getCachedOrEnvAppDownloads, resolveDownloadUrl, type AppDownloads } from '@/utils/appDownloadService';

const isValidEgyptPhone = (phone: string) => {
  const digits = phone.replace(/\D/g, '');
  return /^01(0|1|2|5)\d{8}$/.test(digits);
};

export default function AuthPage() {
  const [searchParams] = useSearchParams();
  const navigate = useNavigate();
  const mode = searchParams.get('mode') || 'signin';
  const [isSignUp, setIsSignUp] = useState(mode === 'signup');
  const [loading, setLoading] = useState(false);
  const [appDownloads, setAppDownloads] = useState<AppDownloads>(getCachedOrEnvAppDownloads());
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    fullName: '',
    phone: ''
  });

  const { signIn, signUp, user } = useFirebaseAuth();

  // استرجاع آخر بريد مستخدم وتطبيقه على واجهة تسجيل الدخول
  useEffect(() => {
    try {
      const last = typeof window !== 'undefined' ? window.localStorage.getItem('cashy:lastEmail') : null;
      if (last) setFormData(prev => ({ ...prev, email: last, password: '' }));
    } catch {}
  }, []);
  const { toast } = useToast();

  if (user) return <Navigate to="/dashboard-redirect" replace />;

  useEffect(() => {
    (async () => {
      try {
        const data = await getAppDownloads();
        setAppDownloads(data || {});
      } catch (e) {
        console.error('Failed to load app downloads', e);
      }
    })();
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    // حارس الضغط أثناء التنفيذ
    if (loading) {
      toast({ title: 'جارٍ التنفيذ', description: 'يرجى الانتظار حتى تكتمل العملية' });
      return;
    }
    // فحص الاتصال قبل البدء
    if (typeof navigator !== 'undefined' && navigator.onLine === false) {
      toast({ title: 'لا يوجد اتصال', description: 'تحقق من اتصال الإنترنت ثم أعد المحاولة', variant: 'destructive' });
      return;
    }

    setLoading(true);

    try {
      if (isSignUp) {
        // تطبيع البريد وكلمة المرور قبل الإنشاء
        const normalizedEmail = (formData.email || '').trim().toLowerCase();
        const normalizedPassword = (formData.password || '').trim();
        // تحقق رقم مصري 11 رقم يبدأ بـ 010/011/012/015
        if (!isValidEgyptPhone(formData.phone)) {
          toast({ title: 'رقم غير صالح', description: 'يجب أن يكون رقم مصري 11 رقم ويبدأ بـ 010/011/012/015', variant: 'destructive' });
          throw new Error('رقم الهاتف غير صالح');
        }
        const { error } = await signUp(normalizedEmail, normalizedPassword, formData.fullName, formData.phone);
        if (error) throw error;
        

        
        toast({ title: "تم إنشاء الحساب بنجاح!", description: "تحقق من بريدك الإلكتروني لتفعيل الحساب" });
        
        // التوجيه إلى صفحة إعداد حساب المستخدم مع تمرير الاسم
        const userName = formData.fullName || 'التاجر الجديد';
        navigate(`/user/account-setup?user_name=${encodeURIComponent(userName)}`);
      } else {
        const normalizedEmail = (formData.email || '').trim().toLowerCase();
        const normalizedPassword = (formData.password || '').trim();
        if (!/^\S+@\S+\.\S+$/.test(normalizedEmail)) {
          throw new Error('صيغة البريد الإلكتروني غير صحيحة');
        }
        const { error } = await signIn(normalizedEmail, normalizedPassword);
        if (error) {
          const code = (error as any)?.code;
          if (code === 'auth/invalid-email') {
            throw new Error('صيغة البريد الإلكتروني غير صحيحة');
          } else if (code === 'auth/user-not-found') {
            throw new Error('لا يوجد حساب بهذا البريد الإلكتروني');
          } else if (code === 'auth/wrong-password') {
            throw new Error('كلمة المرور غير صحيحة');
          } else if (code === 'auth/invalid-credential') {
            throw new Error('البريد الإلكتروني أو كلمة المرور غير صحيحة');
          } else if (code === 'auth/too-many-requests') {
            throw new Error('تم تجاوز عدد المحاولات المسموح، حاول لاحقاً');
          }
          throw error;
        }
        toast({ title: "مرحباً بك!", description: "تم تسجيل الدخول بنجاح، جاري إعداد حسابك..." });
      }
    } catch (error: any) {
      toast({ 
        title: "خطأ", 
        description: error?.message || "حدث خطأ أثناء المعالجة",
        variant: "destructive"
      });
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="min-h-screen bg-background flex items-center justify-center p-4">
      <Card className="w-full max-w-md">
        <CardHeader className="text-center">
          <div className="flex items-center justify-center gap-2 mb-4">
            <BrandLogo size={32} />
            <span className="text-xl font-bold">CashyLink</span>
          </div>
          <CardTitle>{isSignUp ? 'إنشاء حساب جديد' : 'تسجيل الدخول'}</CardTitle>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleSubmit} className="space-y-4">
            {isSignUp && (
              <>
                <Input
                  placeholder="الاسم الكامل"
                  value={formData.fullName}
                  onChange={(e) => setFormData({ ...formData, fullName: e.target.value })}
                  required
                />
                <Input
                  placeholder="رقم مصري (11 رقم يبدأ بـ 010/011/012/015)"
                  value={formData.phone}
                  onChange={(e) => setFormData({ ...formData, phone: e.target.value })}
                  dir="ltr"
                  inputMode="numeric"
                  required
                />
              </>
            )}
            <Input
              type="email"
              placeholder="البريد الإلكتروني"
              value={formData.email}
              onChange={(e) => {
                const v = e.target.value;
                setFormData({ ...formData, email: v });
                try { window.localStorage.setItem('cashy:lastEmail', v); } catch {}
              }}
              required
            />
            <Input
              type="password"
              placeholder="كلمة المرور"
              value={formData.password}
              onChange={(e) => setFormData({ ...formData, password: e.target.value })}
              autoComplete={isSignUp ? 'new-password' : 'off'}
              required
            />
            <Button type="submit" className="w-full btn-primary" onClick={(e) => {
              // فحص مسبق لعرض سبب المنع
              if (loading) {
                e.preventDefault();
                e.stopPropagation();
                toast({ title: 'جارٍ التنفيذ', description: 'يرجى الانتظار حتى تكتمل العملية' });
                return;
              }
              if (typeof navigator !== 'undefined' && navigator.onLine === false) {
                e.preventDefault();
                e.stopPropagation();
                toast({ title: 'لا يوجد اتصال', description: 'تحقق من اتصال الإنترنت ثم أعد المحاولة', variant: 'destructive' });
              }
            }}>
              {loading ? 'جاري المعالجة...' : (isSignUp ? 'إنشاء الحساب' : 'تسجيل الدخول')}
            </Button>
            {/* زر تسجيل دخول كمدير تمت إزالته لعدم حفظ كلمة المرور تلقائياً ولأمان أعلى */}
          </form>
          
          <div className="text-center mt-6">
            <span className="text-muted-foreground">
              {isSignUp ? 'لديك حساب؟' : 'ليس لديك حساب؟'}
            </span>
            <Button 
              variant="link" 
              onClick={() => setIsSignUp(!isSignUp)}
              className="text-primary"
            >
              {isSignUp ? 'تسجيل الدخول' : 'إنشاء حساب'}
            </Button>
          </div>

          <div className="text-center mt-4">
            <Link to="/">
              <Button variant="ghost" className="text-muted-foreground">
                العودة للصفحة الرئيسية
              </Button>
            </Link>
          </div>

          {/* Download buttons visible to all */}
          <div className="mt-6 flex flex-col sm:flex-row gap-3 justify-center items-center">
            <a
              href={resolveDownloadUrl(appDownloads?.androidUrl) || '#'}
              onClick={(e) => { if (!appDownloads?.androidUrl) e.preventDefault(); }}
              className={`flex items-center gap-3 px-4 py-2 rounded-lg shadow-elegant transform hover:scale-105 transition-all duration-300 ${appDownloads?.androidUrl ? 'bg-green-600 text-white hover:bg-green-700' : 'bg-gray-300 text-gray-600 cursor-not-allowed'}`}
              aria-disabled={!appDownloads?.androidUrl}
              target="_blank"
              rel="noopener noreferrer"
            >
              <Smartphone className="h-5 w-5" />
              تحميل للأندرويد
              <Download className="h-4 w-4" />
            </a>
            <a
              href={resolveDownloadUrl(appDownloads?.pcUrl) || '#'}
              onClick={(e) => { if (!appDownloads?.pcUrl) e.preventDefault(); }}
              className={`flex items-center gap-3 px-4 py-2 rounded-lg shadow-elegant transform hover:scale-105 transition-all duration-300 ${appDownloads?.pcUrl ? 'bg-indigo-600 text-white hover:bg-indigo-700' : 'bg-gray-300 text-gray-600 cursor-not-allowed'}`}
              aria-disabled={!appDownloads?.pcUrl}
              target="_blank"
              rel="noopener noreferrer"
            >
              <Monitor className="h-5 w-5" />
              تحميل تطبيق الكمبيوتر نسخة 64 بت
              <Download className="h-4 w-4" />
            </a>
            <a
              href={resolveDownloadUrl(appDownloads?.pc32Url) || '#'}
              onClick={(e) => { if (!appDownloads?.pc32Url) e.preventDefault(); }}
              className={`flex items-center gap-3 px-4 py-2 rounded-lg shadow-elegant transform hover:scale-105 transition-all duration-300 ${appDownloads?.pc32Url ? 'bg-blue-600 text-white hover:bg-blue-700' : 'bg-gray-300 text-gray-600 cursor-not-allowed'}`}
              aria-disabled={!appDownloads?.pc32Url}
              target="_blank"
              rel="noopener noreferrer"
            >
              <Monitor className="h-5 w-5" />
              تحميل تطبيق الكمبيوتر نسخة 32 بت
              <Download className="h-4 w-4" />
            </a>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
import BrandLogo from '@/components/BrandLogo';
